import {GameData} from './data/game-data';

class GameTimer {
  constructor(sec) {
    this.sec = sec;
  }

  getTime() {
    return this.sec;
  }

  tick() {
    this.sec -= GameData.STEP;
    if (this.sec <= GameData.DEFAULT_VALUE) {
      return `time is over`;
    }
    return this.sec;
  }

}

export default GameTimer;
