import App from '../../application';
import StatsView from './stats-view';
import Loader from '../../loader';
import data from './stats-data';
import {renderScreen, resetState, countPoints, GameData} from '../../data/game-data';

class StatsScreen {

  init(state) {
    const currentGameState = {
      answers: state.answers,
      lives: state.lives,
      result: state.lives <= GameData.MIN_NUM_OF_LIVES ? data.title.fail : countPoints(state.answers, state.lives).all
    };

    let view;

    Loader.saveResults(currentGameState, state.userName).
        then(() => Loader.loadResults(state.userName)).
        then((results) => {
          view = new StatsView(state, results);
          view.backButtonClickHandler = () => {
            resetState(state);
            App.showGreetingScreen();
          };
        }).then(() => {
          renderScreen(view.element);
        });
  }
}

export default StatsScreen;
