import IntroView from './intro-view';
import {addElementToCentral} from '../../data/game-data';

class IntroScreen {
  constructor() {
    this.view = new IntroView();
  }

  init() {
    addElementToCentral(this.view.element);
  }

  hide() {
    this.view.hide();
  }
}

export default new IntroScreen();
