const adaptServerData = (data) => {
  return data.map(({type, question, answers}) => {
    return {
      type,
      question,
      questions: answers.map(({image, type: imageType}) => {
        return {
          url: image.url,
          width: image.width,
          height: image.height,
          answer: imageType
        };
      })
    };
  });
};

export default adaptServerData;
