import IntroScreen from './screens/intro/intro-screen';
import GreetingScreen from './screens/greeting/greeting-screen';
import RulesScreen from './screens/rules/rules-screen';
import GameScreen from './screens/game/game-screen';
import StatsScreen from './screens/stats/stats-screen';
import ErrorScreen from './screens/components/error-screen';
import Loader from './loader';
import adaptServerData from './data/data-adapter';
import {preloadImages, GameData} from './data/game-data';

class Application {

  constructor() {
    this.NewGameScreen = GameData.DEFAULT_VALUE;
  }

  static start() {
    this.showIntroScreen();
    Loader.loadData().
        then(adaptServerData).
        then((data) => {
          return preloadImages(data);
        }).
        then((data) => {
          this.NewGameScreen = new GameScreen(data);
          return this.NewGameScreen;
        }).
        then(() => IntroScreen.hide()).
        then(() =>
          setTimeout(() => {
            GreetingScreen.init();
            GreetingScreen.show();
          }, 300)).
        catch(Application.showError);
  }

  static showError(error) {
    return new ErrorScreen(error.message).show();
  }

  static showIntroScreen() {
    Loader.loadData();
    IntroScreen.init();
  }

  static showRulesScreen() {
    Loader.loadData();
    GreetingScreen.deleteAnimation();
    RulesScreen.init();
  }

  static showGreetingScreen() {
    GreetingScreen.init();
  }

  static showGameScreen() {
    this.NewGameScreen.init();
  }

  static showStatsScreen(state) {
    new StatsScreen().init(state);
  }
}

export default Application;
